.App {
  /*text-align: center;*/
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.search-bar {
  float: left;
}

.calendar-container {
  width: 250px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  background: rgba(1, 1, 1, 0.8);
  border: 2px solid #eee;
  border-radius: 10px;
  padding: 10px;
}

input#month-picker {
  border: 1px solid #e0e0e0;
  background-color: #f6f6f6;
  padding-left: 0.9rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  line-height: 2.8rem;
  font-size: 1.3rem;
  color: #525252;
}

/*  SECTIONS  */
.section_mp {
  clear: both;
  padding: 0px;
  margin: 0px;
}

/*  COLUMN SETUP  */
.col_mp {
  display: block;
  float:left;
  text-align: center;
  color: #ddd;
}
.col_mp:first-child {
  margin-left: 0;
}

/*  GROUPING  */
.group_mp:before, .group:after {
  content:"";
  display:table;
}
.group_mp:after {
  clear:both;
}
.group_mp {
  zoom:1; /* For IE 6/7 */
}

/*  GRID OF THREE  */
.span_1_of_3_mp { width: 33.33%; }

.col_mp{
  font-size: 16px;
  /*padding-bottom: 5px;*/
  /*padding-top:5px;*/
  border-radius: 5px;
  padding: 10px 0;
}

.col_mp:hover{
  color:white;
  /*background-color: darkslateblue;*/
  background-color: #999;
  cursor: pointer;
}
.selected_date_mp{
  font-size:18px;
  color:darkslateblue;
  font-weight: bold;
  color: #eee;
}
.selected_cell{
  /*background-color: darkslateblue;*/
  background-color: rgba(189, 211, 242, 0.7);
  font-style: italic;
  color: #fff;
}
.arrows_mp{
  font-weight: bold;
  font-size: 18px;
}


